import React from 'react'
import ExternalLink from './ExternalLink'

interface PillProps {
    text: string
}

const Pill: React.FC<PillProps> = ({ text }) => {
    return (
        <span
            css={{
                backgroundColor: '#ab9df2',
                fontSize: '0.8rem',
                height: '1.4rem',
                padding: '0.2rem 0.5rem',
                marginRight: '0.5rem',
                borderRadius: '0.7rem',
            }}
        >
            {text}
        </span>
    )
}

interface ProjectEntryProps {
    title: string
    url?: string
    linkTitle?: string
    pills?: string[]
    description?: string
}

const ProjectEntry: React.FC<ProjectEntryProps> = ({
    title,
    url,
    linkTitle,
    pills,
    description,
}) => {
    return (
        <div css={{ marginBottom: '1.5rem' }}>
            <div>
                <h3
                    css={{
                        marginRight: '0.75rem',
                        marginBottom: '0.5rem',
                        display: 'inline-block',
                    }}
                >
                    {title}
                </h3>
                {url && (
                    <ExternalLink href={url}>{linkTitle || url}</ExternalLink>
                )}
            </div>
            {pills.map((text, index) => (
                <Pill key={index} text={text} />
            ))}
            <div>{description}</div>
        </div>
    )
}

export default ProjectEntry
